import React from "react";
import { Box, Button, Flex, Image, Link, Spacer } from '@chakra-ui/react';
import LinkedIn from "./assets/social-media-icons/linkedin.png";
import Email from "./assets/social-media-icons/email.png";
import Twitter from "./assets/social-media-icons/twitter.png";
import "./assets/navbar.css"



const NavBar = ({ accounts, setAccounts }) => {
    const isConnected = Boolean(accounts[0]);

    async function connectAccount() {
        if (window.ethereum) {
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",
            });
            setAccounts(accounts);
        }
    }

    return (
        <Flex justify="space-between" align="center" padding="30px">
            {/* left side */}
            <Flex justify="space-around" width="40%" padding="0 75px">
                <Link href="https://www.linkedin.com/in/caden-smith-7806a3223/">
                    <Image src={LinkedIn} boxSize="42px" margin="0 15px" />
                </Link>
                <Link href="mailto:cadensmith1123@gmail.com">
                    <Image src={Email} boxSize="42px" margin="0 15px" />
                </Link>
                <Link href="https://twitter.com/Boreinc23">
                    <Image src={Twitter} boxSize="42px" margin="0 15px" />
                </Link>



            </Flex>
          

        {/* right side */}
        
        <Flex justify="space-around"
        align="center"
        width="40%"
        padding="30px"
        >

        <Box className="hide" margin="0 15px">About</Box>
        <Spacer />
        <Box className="hide" margin="0 15px">Mint</Box>
        <Spacer />
        <Box className="hide" margin="0 15px">Team</Box>
        <Spacer />



          {/* Connect */}
          {isConnected ? (
             <Box className="nav" margin="0 15px">Connected</Box>
         ) : (
             <Button 
             backgroundColor="#364b3a"
             borderRadius="5px"
             boxShadow="0px 2px 2px 1px #0F0F0F"
             color="white"
             cursor="pointer"
             fontFamily="inherit"
             padding="15px"
             margin="0 15px"
             onClick={connectAccount}>Connect</Button>
         )}


         </Flex>



      
      
        </Flex>
    )
}

export default NavBar;