import React from 'react'
import { useState } from 'react'
import { ethers, BigNumber } from 'ethers';
import { Box, Button, Flex, Input, Text } from '@chakra-ui/react';
import silverFallsNFT from './SilverFallsNFT.json';
import "./assets/mainmint.css"



const silverFallsNFTAddress = "0x02Cd63D50901c39Ed0abD1a38A5F88Fb012CDD12";

const MainMint = ({ accounts, setAccounts }) => {
    const [mintAmount, setMintAmount] = useState(1);
    const isConnected = Boolean(accounts[0]);

    async function handleMint() {
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                silverFallsNFTAddress,
                silverFallsNFT.abi,
                signer
            );
            try {
                const response = await contract.mint(BigNumber.from(mintAmount), {
                    value: ethers.utils.parseEther((0.02 * mintAmount).toString()),
                });
                console.log('response: ', response);
            } catch (err) {
                console.log("error: ", err)
            }
        }
    }

    const handleDecrement = () => {
        if (mintAmount <= 1) return;
        setMintAmount(mintAmount - 1);
    };

    const handleIncrement = () => {
        if (mintAmount >= 3) return;
        setMintAmount(mintAmount + 1);
    };

    return (
        <Flex justify="center" align="center" height="100vh" paddingBottom="150px">
            <Box width="520px">
                <div>
            <Text className='title' fontSize="48px" textShadow="0 5px #000000">SilverFalls</Text>
            <Text className='main'
            letterSpacing="-5.5%"
            fontFamily="VT323"
            textShadow="0 2px 2px #000000"
            >
                It's 1938. Will the SilverFalls NFT deliver fortune to the historic lands? Mint SilverFalls to witness prosperity
                </Text>
            </div>

            {isConnected ? (
                <div>
                    <Flex align="center" justify="center">
                        <Button 
                        backgroundColor="#364b3a"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1px #0F0F0F"
                        color="white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        marginTop="10px"
                        onClick={handleDecrement}>-</Button>

                        <Input 
                        readOnly
                        fontFamily="inherit"
                        width="100px"
                        height="40px"
                        textAlign="center"
                        paddingLeft="19px"
                        marginTop="10px"
                        type="number" 
                        value={mintAmount}/>

                        <Button 
                        backgroundColor="#364b3a"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1px #0F0F0F"
                        color="white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        marginTop="10px" 
                        onClick={handleIncrement}>
                            +
                            </Button>


                    </Flex>

                     <Button 
                        backgroundColor="#364b3a"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1px #0F0F0F"
                        color="white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        marginTop="10px"  
                        onClick={handleMint}>Mint Now
                        </Button>
                </div>

            ) : (
                <Text
                className='subhead'
                marginTop="70px"
                fontSize="30px"
                letterSpacing="-5.5%"
                fontFamily="VT323"
                textShadow="0 3px #000000"
                color="#364b3a"
                >
                Must be connected to Mint
                </Text>
            )}
            </Box>
        </Flex>
    );
};

export default MainMint;



